<template>
  <div v-loading="loading" element-loading-text="拼命加载中">
    <div class="handle-box">
      <div class="btn">
        <el-input
          placeholder="输入学生学号/姓名"
          v-model="student"
          @keyup.enter.native="search(student)"
          class="input-with-select"
        >
          <el-select
            v-model="type"
            placeholder="请选择搜索方式"
            slot="prepend"
            @change="changeType"
            style="width: 150px"
          >
            <el-option
              v-for="item in typeList"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
          <el-button
            slot="append"
            icon="el-icon-search"
            @click="search(student)"
          ></el-button>
        </el-input>
      </div>
    </div>
    <el-table
      :data="tableData"
      ref="multipleTable"
      border
      :header-cell-style="{ background: '#f0f0f0' }"
      max-height="620"
    >
      <el-table-column prop="number" label="学号"></el-table-column>
      <el-table-column prop="name" label="姓名"></el-table-column>
      <el-table-column
        prop="patriarch_count"
        label="家长数量"
      ></el-table-column>
      <el-table-column prop="state" label="审核状态">
        <template slot-scope="scope">
          <span v-if="scope.row.state == 1">未提交</span>
          <span v-else-if="scope.row.state == 2">待审核</span>
          <span v-else-if="scope.row.state == 3">审核通过</span>
          <span v-else-if="scope.row.state == 4">审核未通过</span>
        </template>
      </el-table-column>
      <el-table-column prop="products" label="是否购买服务">
        <template slot-scope="scope">
          <span v-if="scope.row.products == 1">已购买服务</span>
          <span v-else-if="scope.row.products == 2">未购买服务</span>
        </template>
      </el-table-column>
      <el-table-column prop="face_image" label="人脸信息">
        <template slot-scope="scope">
          <el-popover placement="top-start" title trigger="click">
            <img
              :src="scope.row.face_image"
              style="max-width: 800px; max-height: 800px"
            />
            <img
              slot="reference"
              :src="scope.row.face_image"
              style="max-width: 180px; max-height: 80px"
            />
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleFamily(scope.$index, scope.row)"
            >家长信息</el-button
          >
          <el-button
            size="mini"
            type="primary"
            @click="handleRecord(scope.$index, scope.row)"
            >消息推送记录</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <el-dialog
      :visible.sync="dialogFamily"
      title="家长信息"
      :close-on-click-modal="false"
      width="80%"
      align="center"
    >
      <el-table
        :data="tableList"
        max-height="620"
        border
        :header-cell-style="{ background: '#f0f0f0' }"
      >
        <el-table-column prop="user_id" label="用户ID"></el-table-column>
        <el-table-column prop="student_id" label="学生ID"></el-table-column>
        <el-table-column prop="name" label="姓名"></el-table-column>
        <el-table-column prop="nickname" label="昵称"></el-table-column>
        <el-table-column prop="accredit" label="是否授权">
          <template slot-scope="scope">
            <span v-if="scope.row.accredit == 1">已授权</span>
            <span v-else-if="scope.row.accredit == 2">未授权</span>
          </template>
        </el-table-column>
        <el-table-column prop="master" label="家长性质">
          <template slot-scope="scope">
            <span v-if="scope.row.master == 1">默认家长</span>
            <span v-else-if="scope.row.master == 2">家庭成员</span>
          </template>
        </el-table-column>
      </el-table>
    </el-dialog>

    <el-dialog
      :visible.sync="dialogRecord"
      title="消息推送记录"
      :close-on-click-modal="false"
      width="80%"
      align="center"
    >
      <el-table
        :data="recordList"
        max-height="620"
        border
        :header-cell-style="{ background: '#f0f0f0' }"
      >
        <el-table-column prop="student_id" label="学生ID"></el-table-column>
        <el-table-column prop="remark" label="推送内容"></el-table-column>
        <el-table-column prop="state" label="消息推送状态">
          <template slot-scope="scope">
            <span v-if="scope.row.state == 1">待推送</span>
            <span v-else-if="scope.row.state == 2">推送成功</span>
            <span v-else-if="scope.row.state == 3">推送失败</span>
          </template>
        </el-table-column>
        <el-table-column prop="created_at" label="创建时间">
          
        </el-table-column>
      </el-table>
    </el-dialog>
    <div class="block">
      <el-pagination
        @current-change="currentChange"
        :current-page.sync="current"
        :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="size"
        layout="sizes, prev, pager, next, jumper"
        :total="total"
        @size-change="sizeChange"
      ></el-pagination>
    </div>
  </div>
</template>

<script>
import API from "@/api/index.js";

export default {
  data() {
    return {
      loading: true,
      tableData: [],
      dialogFamily: false,
      dialogRecord: false,
      tableList: [],
      stuInfo: {
        name: "",
        number: "",
      },
      current: 1,
      size: 10,
      total: 0,
      type: 2,
      typeList: [
        {
          // 搜索方式
          value: 1,
          label: "按学生学号搜索",
        },
        {
          value: 2,
          label: "按学生姓名搜索",
        },
      ],
      student: "",
      recordList: []
    };
  },
  mounted() {
    this.getInfo();
  },
  methods: {
    getInfo() {
      var self = this;
      API.gainStuInfo(self.current, self.size)
        .then((res) => {
          // console.log(res);
          self.loading = false;
          self.tableData = res.data;
          self.total = res.total;
        })
        .catch((err) => {
          self.loading = false;
        });
    },
    currentChange(val) {
      var self = this;
      self.current = val;
      self.loading = true;
      if (self.student) {
        switch (self.type) {
          case 1:
            var number = self.student;
            API.serStuInfo(val, self.size, name, number).then((res) => {
              self.loading = false;
              self.$message.success("搜索成功！");
              self.tableData = res.data;
              self.total = res.total;
            });
            break;
          case 2:
            var name = self.student;
            API.serStuInfo(val, self.size, name, number).then((res) => {
              self.loading = false;
              self.$message.success("搜索成功！");
              self.tableData = res.data;
              self.total = res.total;
            });
            break;
        }
      } else {
        API.gainStuInfo(val, self.size)
          .then((res) => {
            // console.log(res);
            self.loading = false;
            self.tableData = res.data;
            self.total = res.total;
          })
          .catch((err) => {
            self.loading = false;
          });
      }
    },
    sizeChange(val) {
      var self = this;
      self.size = val;
      self.loading = true;

      if (self.student) {
        switch (self.type) {
          case 1:
            var number = self.student;
            API.serStuInfo(self.current, val, name, number).then((res) => {
              self.loading = false;
              self.$message.success("搜索成功！");
              self.tableData = res.data;
              self.total = res.total;
            });
            break;
          case 2:
            var name = self.student;
            API.serStuInfo(self.current, val, name, number).then((res) => {
              self.loading = false;
              self.$message.success("搜索成功！");
              self.tableData = res.data;
              self.total = res.total;
            });
            break;
        }
      } else {
        API.gainStuInfo(self.current, val)
          .then((res) => {
            // console.log(res);
            self.loading = false;
            self.tableData = res.data;
            self.total = res.total;
          })
          .catch((err) => {
            self.loading = false;
          });
      }
    },
    handleFamily(index, row) {
      var self = this;
      self.dialogFamily = true;
      self.tableList = self.tableData[index].patriarch;
      // console.log(self.tableList);
    },
    // 搜索方式
    changeType(val) {
      var self = this;
      self.typeDisabled = true;
      self.student = "";
    },
    search() {
      var self = this;
      self.loading = true;
      switch (self.type) {
        case 1:
          self.current = 1;
          var number = self.student;
          API.serStuInfo(self.current, self.size, name, number).then((res) => {
            self.loading = false;
            self.$message.success("搜索成功！");
            self.tableData = res.data;
            self.total = res.total;
          });
          break;
        case 2:
          var name = self.student;
          self.current = 1;
          API.serStuInfo(self.current, self.size, name, number).then((res) => {
            self.loading = false;
            self.$message.success("搜索成功！");
            self.tableData = res.data;
            self.total = res.total;
          });
          break;
      }
    },
    handleRecord(index, row) {
      var self = this;
      self.dialogRecord = true;
      // console.log(self.tableData[index]);
      self.recordList = self.tableData[index].access;
      // console.log(1,self.recordList);
    },
  },
};
</script>

<style lang="scss" scoped>
.upload-btn {
  margin-top: 10px;
}

.pic-box {
  max-width: 100%;
  height: 200px;
}
.table .trHeight {
  height: 40px;
  text-align: center;
}
.bg {
  background-color: #ebeef5;
}
</style>